.Wrapper {
    width: 100%;
    height: 80vh;
    position: sticky;
    top: 92px;
}
.Wrapper_image {
    width: 100%;
    height: 100%;
    background-image: url(../../media/mountains_wrapper.png);
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    /* object-fit: cover; */
    border-radius: 40px;
}
.Wrapper_block_GEOTEC {
    width: 50%;
    height: 80%;
    position: absolute;
    top: 69px;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: flex-start;
}
.Wrapper_block_GEOTEC_block1 {
    height: auto;
    width: auto;
    position: relative;
    left: 43px;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}
.Wrapper_block_GEOTEC_title {
    width: 90%;
    text-transform: uppercase;
    font-size: 164px;
    font-family: 'Poppins-ExtraLight';
    color: #3856B5;
    line-height: 246px;
    font-weight: 275;
    font-style: normal;
}
.Wrapper_block_GEOTEC_logo {
    width: 10%;
    position: relative;
}